import ProjectI from '../models/project'
import projectsJson from '../projects.json'
import { Grid, Typography, Box, Chip } from '@mui/material'
import { useTheme } from '@mui/material/styles';
import { ReactNode } from 'react'
import ColorfulPages from './ColorfulPages'
import TaggedBox from './TaggedBox'

const parsedProjects: ProjectI[] = projectsJson

export default function ProjectsShowcase() {

    const theme = useTheme()


    const getSpecial = (title: string): ReactNode => {
        switch (title) {
            case "Colorful pages":
                return <ColorfulPages/>
            case "TaggedBox":
                return <TaggedBox/>
            default: break
        }
        return <></>
    } 

    const getTags = (languages: string[], frameworks: string[], tools: string[]): ReactNode => {
        return (<div style={{display:'flex', flexDirection:'column', gap:'8px'}}>
        <Typography fontSize={'0.9em'} color={'grey'}>Tech stack</Typography>
        <div style={{display:'flex', gap:"8px", flexWrap:'wrap'}}>
            {languages.map((item) => {
                return <Chip label={item}/>
            })}    
            {frameworks.map((item) => {
                return <Chip label={item}/>
            })}    
            {tools.map((item) => {
                return <Chip label={item}/>
            })}    
        </div>
            </div>
        )

    }


    return (
        <Grid container spacing={2} paddingBottom={8}>
            { parsedProjects.map((project) => {
                return (
                    <Grid item xs={12} md={6}>
                        <Box sx={{background: theme.palette.background.paper , p:4, borderRadius:1, display:'flex', flexDirection:'column',gap:"16px" }}>
                            <div style={{display:'flex', flexDirection:'column'}}>
                            <div style={{display:'flex', flexDirection:'row', gap:'16px', alignItems:'start'}}>
                                <Typography fontSize={"1.2em"}>{project.title}</Typography>
                                <Chip label={project.type} color={project.type === 'work' ? 'secondary' : project.type === 'personal' ? 'primary' : 'warning'} variant='outlined'/>
                            </div>
                            <Typography fontSize={'0.9em'} color={'grey'}>{project.year}</Typography>
                            </div>
                            <Typography>{project.description}</Typography>
                                {getSpecial(project.title)}
                                {getTags(project.languages,project.frameworks,project.tools)}
                        </Box>
                    </Grid>
                )
            })
            }
      </Grid>
    )
}